<template>
  <div>
    <!-- Header -->
    <notifications></notifications>
    <img
      style="width: 50%; position: absolute; top: 0; left: 0"
      src="/img/brand/tk_login_pic.png"
    />
    <!-- Page content -->
    <div class="container" style="position: relative; z-index: 1">
      <div class="row justify-content-center">
        <div class="tk-bg-color-white tk-px-margin-top-px100 tk-text-center">
          <img
            src="/img/brand/tk_logo_icon.png"
            class="tk-px-width-px90"
            v-if="step !== 2"
          />
          <avatar
            :uId="model.userId"
            :studioId="model.studioId"
            :name="model.fullName"
            v-else
          ></avatar>
          <fade-transition>
            <div
              class="tk-font-color-orange tk-px-margin-top-px30 text-lg"
              v-show="failed"
            >
              <span class="tk-opacity-0">0</span>
              <span class="text-sm">{{ failedMsg }}</span>
              <span class="tk-opacity-0">0</span>
            </div>
          </fade-transition>
          <fade-transition>
            <div
              class="tk-font-color-lightGray tk-px-margin-top-px30 text-lg"
              v-show="!failed"
            >
              <span v-if="step !== 2">Get started</span>
              <span v-else>{{ model.fullName }}</span>
            </div>
          </fade-transition>
        </div>
      </div>
      <div class="row justify-content-center">
        <div class="col-lg-5 col-md-7 tk-px-margin-top-px30">
          <validation-observer ref="formValidator">
            <form role="form" style="width: 335px; margin: 0 auto">
              <!-- account -->
              <template v-if="step === 1">
                <base-input
                  alternative
                  name="Email"
                  :rules="{ required: false, email: true }"
                  prepend-icon="ni ni-email-83"
                  placeholder="Your email"
                  v-model="model.email"
                  inputClasses="tk-px-height-px64"
                  inputGroupClasses="tk-border-color-gray-200"
                  id="loginAccount"
                ></base-input>

                <div class="text-center">
                  <!-- <base-button
                    class="my-4 tk-border-none"
                    style="
                      width: 180px;
                      height: 50px;
                      margin-top: 10px !important;
                    "
                    :class="{
                      'tk-bg-color-disableBtn tk-font-color-gray': invalidEmail,
                      'tk-bg-color-main tk-font-color-white': !invalidEmail,
                    }"
                    @click.prevent="next"
                    :loading="loading"
                    >
                      <span v-if="!loading">NEXT</span>
                    </base-button> -->
                  <button-with-loading
                    style="margin: 10px auto 1rem"
                    text="NEXT"
                    @start="next"
                    width="180"
                    :type="invalidEmail ? 'disable' : 'main'"
                    :complete="isButtonLoadingComplete"
                  ></button-with-loading>
                </div>

                <div class="text-center text-muted mb-4 tk-px-margin-top-px0">
                  <small>or</small>
                  <div class="btn-wrapper text-center">
                    <div
                      class="btn btn-neutral btn-icon tk-border-color-gray-200"
                      style="
                        display: block;
                        width: 300px;
                        height: 50px;
                        margin: 20px auto;
                        padding: 0;
                      "
                      @click.prevent="loginWithFacebook"
                    >
                      <span
                        class="btn-inner--icon tk-layout-inline-block tk-font-color-white tk-px-radius-px5"
                        style="
                          background-color: #3b5998;
                          line-height: 50px;
                          width: 50px;
                          height: 50px;
                        "
                      >
                        <i class="fab fa-facebook-f"></i>
                      </span>
                      <span
                        class="btn-inner--text tk-layout-inline-block tk-font-color-lightGray"
                        style="width: calc(100% - 50px); margin-left: 0"
                        >Log In with Facebook</span
                      >
                    </div>
                    <div
                      class="btn btn-neutral btn-icon tk-border-color-gray-200"
                      style="
                        display: block;
                        width: 300px;
                        height: 50px;
                        margin: 14px auto;
                        padding: 0;
                      "
                      @click.prevent="loginWithGoogle"
                    >
                      <span
                        class="btn-inner--icon tk-layout-inline-block tk-font-color-white tk-px-radius-px5"
                        style="
                          background-color: #dc4e41;
                          line-height: 50px;
                          width: 50px;
                          height: 50px;
                        "
                      >
                        <i class="fab fa-google"></i>
                      </span>
                      <span
                        class="btn-inner--text tk-layout-inline-block tk-font-color-lightGray"
                        style="width: calc(100% - 50px); margin-left: 0"
                        >Log In with Google</span
                      >
                    </div>
                    <div
                      class="btn btn-neutral btn-icon tk-border-color-gray-200"
                      style="
                        display: block;
                        width: 300px;
                        height: 50px;
                        margin: 14px auto;
                        padding: 0;
                      "
                      @click.prevent="loginWithApple"
                    >
                      <span
                        class="btn-inner--icon tk-layout-inline-block tk-font-color-white tk-px-radius-px5"
                        style="
                          background-color: #222;
                          line-height: 50px;
                          width: 50px;
                          height: 50px;
                        "
                      >
                        <i class="fab fa-apple"></i>
                      </span>
                      <span
                        class="btn-inner--text tk-layout-inline-block tk-font-color-lightGray"
                        style="width: calc(100% - 50px); margin-left: 0"
                        >Log In with Apple</span
                      >
                    </div>
                  </div>
                </div>
              </template>

              <!-- login -->
              <template v-if="step === 2">
                <!-- :rules="{ required: true, min: 6, max: 6 }"  -->
                <base-input
                  alternative
                  name="Password"
                  :rules="{ required: false, min: 6 }"
                  prepend-icon="ni ni-lock-circle-open"
                  type="password"
                  placeholder="Password"
                  v-model="model.password"
                  inputClasses="tk-px-height-px64"
                  inputGroupClasses="tk-border-color-gray-200"
                  class="tk-form-input-pwd"
                >
                </base-input>

                <div
                  class="tk-text-center tk-font-color-main tk-cursor-pointer tk-font-weight-light tk-text-right"
                  @click.prevent="forgotPwd"
                >
                  Forgot password?
                </div>

                <div class="text-center">
                  <button-with-loading
                    style="margin: 30px auto 10px"
                    text="LOGIN"
                    @start="login"
                    width="180"
                    :type="invalidPwd ? 'disable' : 'main'"
                    :complete="isButtonLoadingComplete"
                  ></button-with-loading>
                  <div
                    class="tk-text-center tk-font-color-main tk-cursor-pointer tk-font-weight-light"
                    @click.prevent="notMe"
                  >
                    Not me?
                  </div>
                </div>
              </template>

              <!-- sign up -->
              <template v-if="step === 3">
                <base-input
                  alternative
                  name="Name"
                  prepend-icon="ni ni-single-02"
                  placeholder="Full name"
                  v-model="model.fullName"
                  inputClasses="tk-px-height-px64"
                  inputGroupClasses="tk-border-color-gray-200"
                ></base-input>

                <base-input
                  alternative
                  name="Password"
                  prepend-icon="ni ni-lock-circle-open"
                  :rules="{ required: false, min: 6, max: 6 }"
                  type="password"
                  placeholder="Password"
                  v-model="model.password"
                  inputClasses="tk-px-height-px64"
                  inputGroupClasses="tk-border-color-gray-200"
                ></base-input>

                <base-input
                  alternative
                  name="Password"
                  prepend-icon="ni ni-lock-circle-open"
                  :rules="{ required: false, min: 6, max: 6 }"
                  type="password"
                  placeholder="Confirm password"
                  v-model="model.password2"
                  class="tk-form-input-pwd"
                  inputClasses="tk-px-height-px64"
                  inputGroupClasses="tk-border-color-gray-200"
                ></base-input>

                <div
                  class="invalid-feedback"
                  v-if="passwordNotSame && !invalidPwd2"
                  style="display: block"
                >
                  The two passwords are not the same
                </div>

                <div class="text-center">
                  <!-- <base-button
                    native-type="submit"
                    class="my-4 tk-border-none"
                    style="
                      width: 180px;
                      height: 50px;
                      margin-top: 30px !important;
                      margin-bottom: 8px !important;
                    "
                    :class="{
                      'tk-bg-color-disableBtn tk-font-color-gray':
                        invalidPwd || invalidPwd2 || model.fullName === '',
                      'tk-bg-color-main tk-font-color-white':
                        !invalidPwd && !invalidPwd2 && model.fullName !== '',
                    }"
                    :loading="loading"
                    @click.prevent="signup"
                  >
                    <span v-if="!loading">SIGN UP</span>
                  </base-button> -->
                  <button-with-loading
                    style="margin: 24px auto 1rem"
                    text="SIGN UP"
                    @start="signup"
                    width="180"
                    :type="
                      invalidPwd || invalidPwd2 || model.fullName === ''
                        ? 'disable'
                        : 'main'
                    "
                    :complete="isButtonLoadingComplete"
                  ></button-with-loading>
                  <div
                    class="tk-text-center tk-font-color-main tk-cursor-pointer tk-font-weight-light"
                    @click.prevent="notMe"
                  >
                    Already have an account?
                  </div>
                </div>
              </template>
            </form>
          </validation-observer>
        </div>
      </div>
    </div>

    <alert
      :title="this.alert.title"
      :content="this.alert.content"
      :left="this.alert.buttons.left.title"
      :right="this.alert.buttons.right.title"
      v-if="alert.isShow"
      @onLeftTapped="this.alert.buttons.left.action"
      @onRightTapped="this.alert.buttons.right.action"
    >
    </alert>
  </div>
</template>

<script>
/* eslint-disable */
import commons from "../../util/commons";
import $ from "jquery";
import bus from "@/bus.js";
import { FadeTransition } from "vue2-transitions";
import collections from "@/services/collections";
import queryCollection from "@/services/query";

export default {
  components: {
    avatar: () => import("@/tkViews/components/layout/Avatar"),
    buttonWithLoading: () => import("../components/layout/ButtonWithLoading"),
    FadeTransition,
    alert: () => import("../components/Modal/Alert"),
  },
  data() {
    return {
      model: {
        fullName: "",
        email: "",
        password: "",
        password2: "",
        rememberMe: false,
        userId: "",
        studioId: "",
      },
      invalidEmail: true,
      invalidPwd: true,
      invalidPwd2: true,
      email: "zw@gmail.com",
      step: 1,
      passwordNotSame: false,
      loading: false,
      isButtonLoadingComplete: false,
      failed: false,
      failedMsg: "",
      alert: {
        isShow: false,
        title: "",
        content: "",
        buttons: {
          left: {
            title: "",
            action: () => {},
          },
          right: {
            title: "",
            action: () => {},
          },
        },
      },

      isConsoleUser: false,
      consoleAccount: null,
      accountManagement: [],
      isOriginalUser: false,
    };
  },
  async created() {
    let self = this
    await this.getAccountManagement();

    let user = localStorage.getItem("auth_user");
    if (!self.$tools.isNull(user)) {
      if (!self.$tools.isNull(JSON.parse(user))) {

        self.accountManagement.accounts.some((item) => {
          if (item.userId == JSON.parse(user).uid) {
            self.$router.push({
              name: "accountManagement",
            });
            return;
          }
        });

        self.$router.push({
          name: "Dashboard",
        });
      }
    }
  },
  mounted() {
    console.log("进入login页面");
  },
  watch: {
    failed(newVal) {
      if (newVal) {
        setTimeout(() => {
          this.failed = false;
          this.failedMsg = "";
        }, 1000);
      }
    },
    model: {
      handler(newVal, oldVal) {
        if (!this.loading) {
          if (this.$tools.isEmail(newVal.email)) {
            console.log("输入的是email");
            this.invalidEmail = false;
          } else {
            this.invalidEmail = true;
            if ($("#loginAccount").find(".invalid-feedback").length === 0) {
              this.invalidEmail = false;
            }
          }

          let consoleAccounts = this.accountManagement.accounts;
          if (!this.$tools.isNull(consoleAccounts)) {
            consoleAccounts.some((item, i) => {
              if (item.email == newVal.email) {
                this.isConsoleUser = true;
                this.consoleAccount = item;
              }
            });
          }

          if (newVal.password.length >= 6) {
            this.invalidPwd = false;
          } else {
            this.invalidPwd = true;
          }

          if (newVal.password2.length >= 6) {
            this.invalidPwd2 = false;
            if (newVal.password2 === newVal.password) {
              this.passwordNotSame = false;
            } else {
              this.passwordNotSame = true;
            }
          } else {
            this.invalidPwd2 = true;
          }
        }
      },
      deep: true,
    },
  },
  methods: {
    async getAccountManagement() {
      // console.log("获取 控制台管理 账号信息");
      this.accountManagement = await queryCollection.getAccountManagement();
      // console.log(this.accountManagement);
    },
    getUserInfo() {
      return new Promise(async (resolve, reject) => {
        try {
          let userDocs = await this.$firebase
            .firestore()
            .collection("user")
            .where("loginMethod", "array-contains-any", [
              {
                method: 1,
                account: this.model.email,
              },
              {
                method: 2,
                account: this.model.email,
              },
              {
                method: 3,
                account: this.model.email,
              },
              {
                method: 4,
                account: this.model.email,
              },
            ])
            .get();
          let data = [];
          userDocs.forEach((doc) => {
            data.push(doc.data());
          });
          // console.log("获取到的用户列表：", data);
          if (data.length > 0) {
            // console.log("返回第一个用户信息：", data[0]);
            resolve(data[0]);
          } else {
            console.log("用户信息为空");
            resolve(null);
          }
        } catch (error) {
          reject(error);
        }
      });
    },
    getTeacherInfo(userId) {
      return new Promise(async (resolve, reject) => {
        try {
          let teacherDoc = await this.$firebase
            .firestore()
            .collection("teacher_info")
            .doc(userId)
            .get();
          // console.log("获取到的教师信息: ", teacherDoc);
          resolve(teacherDoc.data());
        } catch (error) {
          reject(error);
        }
      });
    },
    async next() {
      let self = this;
      if (!self.invalidEmail && !self.loading) {
        // if (!self.isConsoleUser) {
        self.loading = true;
        let userInfo = await self.getUserInfo();
        // console.log("获取到的用户信息：", userInfo);
        //判断用户是否具有相应权限

        if (userInfo !== null) {
          if (self.$tools.isTeacher(userInfo.roleIds)) {
            let teacherInfo = await self.getTeacherInfo(userInfo.userId);
            // console.log("获取到的教师信息: ", teacherInfo);
            self.isButtonLoadingComplete = true;
            self.step = 2;
            self.model.fullName = userInfo.name;
            self.model.userId = userInfo.userId;
            self.model.studioId = teacherInfo.studioId;
            self.isOriginalUser = true;
          } else {
            self.isButtonLoadingComplete = true;
            console.log("当前身份是学生,退出登录");
          }
        } else {
          console.log("校验email是否正确");
          let isValid = await commons.emailValidVerify(self.model.email);

          console.log("当前email合法性: ", isValid);
          console.log("当前校验结果类型: ", typeof isValid);

          if (self.isConsoleUser) {
            // console.log("获取到的管理员信息: ", this.consoleAccount);
            self.isButtonLoadingComplete = true;
            self.step = 2;
            self.model.fullName = this.consoleAccount.name;
            self.model.userId = "";
            self.model.studioId = "";
          } else {
            if (isValid) {
              if (!self.isConsoleUser) {
                console.log("当前email合法");
                self.isButtonLoadingComplete = true;
                self.step = 3;
                self.model.fullName = "";
              }
            } else {
              console.log("Email 不合法: ", self.model.email);
              self.alert.title = "Wrong email";
              self.alert.content =
                "This email address \n(" +
                self.model.email +
                ") \ndoesn't exist or temporatily couldn't receive message.";
              self.alert.buttons.left.title = "Go Back";
              self.alert.buttons.left.action = () => {
                self.alert.isShow = false;
                self.isButtonLoadingComplete = true;
              };
              self.alert.buttons.right.title = "Continue";
              self.alert.buttons.right.action = () => {
                self.alert.title = "Continue?";
                self.alert.content =
                  "Are you sure to continue with this wrong email address \n(" +
                  self.model.email +
                  ")?";
                self.alert.buttons.left.title = "Go Back";
                self.alert.buttons.right.title = "Continue";
                self.alert.buttons.right.action = () => {
                  self.isButtonLoadingComplete = true;
                  self.step = 3;
                  self.model.fullName = "";
                  self.alert.isShow = false;
                };
                self.alert.isShow = true;
              };
              self.alert.isShow = true;
            }
          }
        }

        self.isButtonLoadingComplete = false;
        self.loading = false;
        // } else {
        //   self.$router.push({
        //     name: "AdministrationLogin",
        //     query: { account: self.account },
        //   });
        // }
      }
    },
    notMe() {
      let self = this;
      // self.model.email = "";
      self.model.fullName = "";
      self.model.password = "";
      self.model.password2 = "";
      // self.invalidEmail = true;
      self.invalidPwd = true;
      self.invalidPwd2 = true;
      self.step = 1;
    },
    forgotPwd() {
      let self = this;
    },
    login() {
      let self = this;
      // console.log(self.model);
      if (!self.$tools.isNull(self.model.email)) {
        self.loading = true;
        // if (self.isOriginalUser) {
        self.$firebase
          .auth()
          .signInWithEmailAndPassword(self.model.email, self.model.password)
          .then((user) => {
            console.log("登陆成功");
            // console.log("登陆成功: ", user);
            self.loading = false;
            self.isButtonLoadingComplete = true;
          })
          .catch((error) => {
            self.isButtonLoadingComplete = true;
            console.error("登陆失败：", error);
          });
        // }
        // else {
        //   if (self.isConsoleUser) {
        //     if (self.model.password == self.consoleAccount.password) {
        //       console.log("管理员身份登录: ", self.consoleAccount);
        //       localStorage.setItem(
        //         "console_user",
        //         JSON.stringify(self.consoleAccount)
        //       );
        //       self.$router.push({
        //         name: "accountManagement",
        //       });
        //     }
        //   }
        // }
      }
      self.isButtonLoadingComplete = false;
      self.loading = false;
    },
    loginWithFacebook() {
      console.log("ready to sign in with Facebook");
      let self = this;
      if (!self.loading) {
        let firebase = self.$firebase;
        var provider = new firebase.auth.FacebookAuthProvider();
        self.signIn(provider);
      }
    },
    loginWithGoogle() {
      console.log("ready to sign in with Google");
      let self = this;
      if (!self.loading) {
        let firebase = self.$firebase;
        let provider = new firebase.auth.GoogleAuthProvider();
        self.signIn(provider);
      }
    },
    loginWithApple() {
      console.log("ready to sign in with Apple");
      let self = this;
      if (!self.loading) {
        let firebase = self.$firebase;
        let provider = new firebase.auth.OAuthProvider("apple.com");
        provider.addScope("email");
        provider.addScope("name");
        self.signIn(provider);
      }
    },
    signIn(provider) {
      let self = this;
      let firebase = self.$firebase;
      firebase
        .auth()
        .signInWithPopup(provider)
        .then((result) => {
          console.log("login success");
          let token = result.credential.accessToken;
          localStorage.setItem("auth_token", token);
          let user = result.user;
          localStorage.setItem("auth_user", user);
        })
        .catch((error) => {
          console.error("login failed:", error);
        });
    },
    async signup() {
      let self = this;
      if (
        !self.$tools.isNull(self.model.fullName) &&
        !self.$tools.isNull(self.model.password) &&
        !self.$tools.isNull(self.model.password2) &&
        !self.loading
      ) {
        console.log("点击注册");
        let isValid = await commons.emailValidVerify(this.model.email);
        if (!isValid) {
          this.$notify({
            message: "The email is wrong",
            type: "warning",
          });
        }
        console.log("校验结果: ", isValid);
        self.loading = true;
        let firebase = self.$firebase;
        let teacherSignup = firebase.functions().httpsCallable("teacherSignup");
        let result = await teacherSignup({
          email: self.model.email,
          password: self.model.password,
          fullName: self.model.fullName,
        });
        console.log("获取到的注册结果: ", result);
        if (result.data) {
          if (result.data.code === 0) {
            this.login();
          } else {
            console.log("sign up failed: ", result);
          }
        } else {
          console.error("sign up failed: ", result);
        }
      }
    },
  },
};
</script>
